<template>
  <div class="container">
    <div class="video">
      <p class="head">视频区</p>
      <hr />
      <ul>
        <li v-for="(item, index) in urls" :key="index">
          <div class="video-list">
            <div class="video-title">
              <a target="_blank" :href="item.url"
                ><img class="video-image" :src="item.img" alt="暂无图片" />{{
                  item.title
                }}</a
              >
            </div>
          </div>
        </li>
      </ul>
    </div>
    <div class="basic">
      <p class="head">五十音图</p>
      <hr />
      <button class="btn btn-info" @click="showFifty('hira')">平假名</button>
      <button class="btn btn-info" @click="showFifty('kata')">片假名</button>
      <div>
        <img
          v-show="showFiftyFlag"
          class="fifty-yin"
          src="static/img/video/50音图（平假名）.png"
          alt="平假名"
        />
        <img
          v-show="!showFiftyFlag"
          class="fifty-yin"
          src="static/img/video/50音图（片假名）.png"
          alt="片假名"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      userName: this.$route.params.userName,
      showFiftyFlag: true,
      urls: [
        {
          title:
            "日本小朋友用这3个洗脑视频学五十音、数字、星期几，朗朗上口，太可爱了吧！",
          url:
            "https://www.bilibili.com/video/BV1BJ411m7RN/?spm_id_from=333.788.recommend_more_video.0",
          img: "static/img/video/五十音图.png",
        },
        {
          title:
            "あいさつのうた　ひらがなの歌で　挨拶の仕方を学ぼう　歌のおやつ",
          url:
            "https://www.bilibili.com/video/BV1uJ411V7dD?from=search&seid=9555160829994231758",
          img: "static/img/video/挨拶.png",
        },
        {
          title: "挨拶之歌-学习简单地用日语打招呼吧【日本儿歌】",
          url:
            "https://www.bilibili.com/video/BV1Bs411i71w?from=search&seid=9555160829994231758",
          img: "static/img/video/挨拶2.png",
        },
        {
          title: "日本一年级小学生必学的80个汉字",
          url:
            "https://www.bilibili.com/video/BV15f4y1d7BQ?from=search&seid=5267205408594124530",
          img: "static/img/video/80汉字.png",
        },
        {
          title: "让我们听听日本小学生唱歌？",
          url:
            "https://www.bilibili.com/video/BV1Kx411u7an?from=search&seid=5267205408594124530",
          img: "static/img/video/日本小学生如何唱歌.png",
        },
        {
          title: "【五十音基础知识汇总】日本小学生是怎么学日语的？",
          url:
            "https://www.bilibili.com/video/BV1aK4y1C799?from=search&seid=5267205408594124530",
          img: "static/img/video/日本小学生如何学日语.png",
        },
      ],
    };
  },
  methods: {
    showFifty(cfc) {
      if (cfc == "hira") {
        this.showFiftyFlag = true;
      } else if (cfc == "kata") {
        this.showFiftyFlag = false;
      }
    },
  },
};
</script>
<style src="@/assets/css/top.css"></style>



