<template>
  <!-- 
  1.出去重复题目
  2.输入框focus后设置边框颜色无效
  3.没有输入姓名，所有按钮非活性
  -->
  <div>
    <!-- <div class="title">我爱数学</div> -->
    <div class="container">
      <div class="math-header-above">
        <label for="name"
          >我的余额:<span class="leftMoney">{{ leftMoney }}</span
          >元</label
        >
      </div>
    </div>
    <hr class="divider" />
    <div class="container font-size18"></div>
    <div class="container">
      <div class="math-header font-size18">
        <!-- 出题数 -->
        <div class="group">
          <span>挑战</span>
          <!-- テキストボックス方式 -->
          <!-- <input
            type="text"
            class="questions-count text-center text-lg-center"
            v-model="questionsCount"
          /> -->
          <select name="count" id="count" v-model="questionsCount">
            <option value="20">20</option>
            <option value="30">30</option>
            <option value="50">50</option>
            <option value="100">100</option>
          </select>
          <span class="font-size18">题</span>
          <label class="spend-time" id="spendTime">00:00:00</label>
          <!-- 出题信息 -->
          <span class="font-size18">
            <!-- 已出题:
            <span>{{ totalCount }}</span> -->
            <span v-show="overFlag">
              正确率：{{ correctCount }}/{{ totalCount }}
              <!-- 正确:
              <span class="blue">{{ correctCount }}</span>
              错误:
              <span class="red">{{ totalCount - correctCount }}</span> -->
            </span>
          </span>
        </div>
        <!-- 题目类型 -->
        <div class="group font-size18">
          题目类型
          <select v-model="questionType">
            <option
              v-for="(item, index) in typeList"
              :key="index"
              :selected="index == 0"
              :value="index"
            >
              {{ item }}
            </option>
          </select>
        </div>
        <!-- 题目难度 -->
        <div class="group font-size18">
          题目难度
          <select v-model="questionLevel">
            <option
              v-for="(item, index) in levelList"
              :key="index"
              :selected="index == 0"
              :value="index"
            >
              {{ item }}
            </option>
          </select>
        </div>
        <!-- <div class="group">
          <span v-show="overFlag">
            <span class="font-size18"
              >你的分数：
              <span class="red">{{ scores }}</span>
            </span>
          </span>
        </div> -->
        <div class="font-size18">
          <label class="radio-inline">
            <input v-model="model" type="radio" name="model" value="1" />计时
          </label>
          <label class="radio-inline">
            <input v-model="model" type="radio" name="model" value="2" />倒计时
          </label>
        </div>
        <div v-show="model == 2">
          <!-- 设置倒计时时间 -->
          Time:{{ limitTime }}分钟
          <input type="range" min="1" max="5" v-model="limitTime" />
        </div>
        <div class="button-groups">
          <!-- <button class="btn btn-info" @click="generateQuestions" :disabled="yourName=''">随机生成题目</button>
          <button class="btn btn-info" @click="resetQuestions" :disabled="yourName=''">清空题目</button>-->
          <button class="btn btn-info" @click="generateQuestions">出题</button>
          <button class="btn btn-info" @click="resetQuestions">清空</button>
          <button
            class="btn btn-danger"
            v-show="model == 1"
            :disabled="!readyFlag"
            @click="start(1)"
          >
            计时
          </button>
          <button
            class="btn btn-danger"
            v-show="model == 2"
            :disabled="!readyFlag"
            @click="start(2)"
          >
            倒计时
          </button>
        </div>
      </div>
      <div class="result-area">
        <div class="result-header">
          <span class="result-title">测试结果（{{ resultCount }}）</span>
          <hr class="divider" />
          <div class="result-content">
            <div
              :class="{
                'result-single': true,
                outstanding: score == 100,
                good: score >= 80,
                pass: score >= 60,
                fail: score < 60,
              }"
              v-for="(score, index) in resultList"
              :key="index"
            >
              {{ score }}
            </div>
          </div>
        </div>
      </div>
      <div class="main-area">
        <table class="table table-striped">
          <thead>
            <tr>
              <th class="col-lg-2 col-xs-2">序号</th>
              <th class="col-lg-4 col-xs-6">题目</th>
              <th class="col-lg-3 col-xs-2">答案</th>
              <th class="col-lg-3 col-xs-2">结果</th>
            </tr>
          </thead>
          <tbody v-show="questionsArray.length == 0">
            <tr>
              <!-- <td v-if="yourName == ''" class="text-center nodata-message" colspan="4">请填写你的姓名。</td>
              <td v-else class="text-center nodata-message" colspan="4">{{yourName}}，你已经准备好了吗？</td>-->
              <td class="text-center nodata-message" colspan="4">
                张嘉程，你已经准备好了吗？
              </td>
            </tr>
          </tbody>
          <tbody v-show="questionsArray.length">
            <tr v-for="(item, index) in questionsArray" :key="index">
              <td class="col-lg-2 col-xs-2 question">{{ index + 1 }}</td>
              <td class="col-lg-4 col-xs-6 question">{{ item.question }} =</td>
              <td class="col-lg-3 col-xs-2 question">
                <input
                  type="text"
                  :id="index"
                  :disabled="!startFlag"
                  :class="{ 'answer-boder': startFlag, 'answer-input': true }"
                />
              </td>
              <td class="col-lg-3 col-xs-2 question">
                <div>
                  <span v-if="item.checkResult == 1">😊</span>
                  <span v-else-if="item.checkResult == 0">😡</span>
                  <span v-else>❔</span>
                  <!-- <span v-show="item.checkResult==1">111</span>
                  <span v-show="item.checkResult==0">222</span>
                  <span v-show="item.checkResult==2">333</span>-->
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <button
        v-show="startFlag"
        class="btn btn-lg btn-danger over-btn"
        @click="submit"
        :disabled="overFlag"
      >
        我做完并且检查完了，我要提交！！！
      </button>
      <button
        v-show="overFlag"
        type="button"
        class="btn btn-info btn-lg calc-btn"
        data-toggle="modal"
        data-target="#myModal"
        :disabled="isCalced"
      >
        计算本次作答奖惩
      </button>

      <!-- Modal -->
      <div
        class="modal fade"
        id="myModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="myModalLabel"
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
              <h4 class="modal-title" id="myModalLabel">奖惩情况</h4>
            </div>
            <div class="modal-body" v-if="reward >= 0">
              本次测试获得<span class="reward-class">{{ reward }}</span
              >元钱
            </div>
            <div class="modal-body" v-else>
              本次测试减去<span class="reward-class">{{
                reward | removeMinus
              }}</span
              >元钱
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-default"
                data-dismiss="modal"
                @click="calcLeftMoney"
              >
                Close
              </button>
              <!-- <button type="button" class="btn btn-primary">
                Save changes
              </button> -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      speedOkFlag: false,
      isCalced: false,
      leftMoney: 0,
      reward: 0,
      message: "頑張ってくださいね！",
      yourName: "",
      timmer: "",
      hour: 0,
      minite: 0,
      seconds: 0,
      // 倒计时时间(minutes)
      limitTime: 1,
      // 倒计时时间(seconds)
      limitTimeSeconds: 60,
      // 答题模式
      model: "1",
      // 答题完成标识
      overFlag: false,
      // 出题完毕标识
      readyFlag: false,
      // 开始答题标识
      startFlag: false,
      // 一次生成题目数
      questionsCount: 20,
      // 出题总数
      totalCount: 0,
      // 作答正确数
      correctCount: 0,
      // 得分
      scores: 0,
      // 选择题目类型
      questionType: 0,
      // 选择题目难度
      questionLevel: "0",
      // 题目类型列表
      typeList: ["加法", "减法", "加减混合"],
      // 题目难度列表
      levelList: ["10以内", "20以内", "50以内", "100以内"],
      // 标准时间（单位：个）
      levelTime: {
        0: { 0: 40, 1: 30, 2: 20, 3: 15 },
        1: { 0: 40, 1: 30, 2: 20, 3: 15 },
        2: { 0: 25, 1: 10, 2: 8, 3: 5 },
      },
      // 题目列表
      questionsArray: [],
      // 今日成绩
      resultList: [],
      // 今日出题个数
      resultCount: 0,
    };
  },

  methods: {
    submit() {
      // stop timmer
      this.stopTimer();
      this.startFlag = false;
      this.overFlag = true;
      // 计算用时
      var spendTime = document.getElementById("spendTime").innerHTML;
      var realSpendTime =
        Number(spendTime.split(":")[1] * 60) + Number(spendTime.split(":")[2]);
      if (this.model == 1) {
        // 计时模式
        console.log("计时模式");
        console.log(realSpendTime);
        // 实际花费时间（秒）
        var spentTime = realSpendTime;
        var countPreMin = (60 / spentTime).toFixed(1) * this.questionsCount;
        console.log(countPreMin);
        var starndTime = this.levelTime[this.questionType][this.questionLevel];
        console.log("starnd time".concat(starndTime));
        if (countPreMin > starndTime) {
          this.speedOkFlag = true;
        } else {
          this.speedOkFlag = false;
        }
      } else if (this.model == 2) {
        // 倒计时模式
        console.log("倒计时模式");
        console.log(this.limitTime);
        console.log(realSpendTime);
        var spentTime = this.limitTime * 60 - realSpendTime;
        var countPreMin = (60 / spentTime).toFixed(1) * this.questionsCount;
        console.log(countPreMin);
        var starndTime = this.levelTime[this.questionType][this.questionLevel];
        console.log("starnd time".concat(starndTime));
        if (countPreMin > starndTime) {
          this.speedOkFlag = true;
        } else {
          this.speedOkFlag = false;
        }
      }
      for (var i = 0; i < this.totalCount; i++) {
        // $("#intro")
        let zjcAnswer = document.getElementById(i).value;
        // 正确
        if (zjcAnswer != "" && this.questionsArray[i].answer == zjcAnswer) {
          this.questionsArray[i].checkResult = 1;
          this.correctCount++;
        } else {
          // 错误
          this.questionsArray[i].checkResult = 0;
        }
      }
      // scores
      this.scores = (this.correctCount / this.totalCount) * 100;
      // 今日测试次数
      this.resultCount++;
      //
      this.resultList.unshift(this.scores);
      // 计算本次作答奖惩状况
      this.calcReward();
    },
    // 计算奖惩
    calcReward() {
      this.reward = 0;
      var starnd = 1;
      switch (this.questionLevel) {
        case 0:
          starnd = 1;
          break;
        case 1:
          starnd = 1.2;
          break;
        case 2:
          starnd = 1.5;
          break;
        case 3:
          starnd = 2;
          break;
        default:
          starnd = 1;
          break;
      }
      if (this.questionType == 2) {
        starnd = starnd * 2;
      }
      if (this.scores == 100) {
        this.reward = 5;
      } else if (this.scores >= 90) {
        this.reward = 3;
      } else if (this.scores >= 80) {
        this.reward = 2;
      } else if (this.scores >= 60) {
        this.reward = -3;
      } else {
        this.reward = -4;
      }
      this.reward = ((this.reward * starnd) / 10).toFixed(1);
      console.log(this.reward);
      if (this.reward > 0 && !this.speedOkFlag) {
        this.reward = 0;
      }
    },
    // 计算本次奖惩
    calcLeftMoney() {
      this.leftMoney = (Number(this.leftMoney) + Number(this.reward)).toFixed(
        1
      );
      console.log(111);
      var savedLeftMoney = localStorage.getItem("leftMoney");
      console.log(savedLeftMoney);
      if (savedLeftMoney != null) {
        localStorage.setItem(
          "leftMoney",
          Number(savedLeftMoney) + Number(this.reward)
        );
      } else {
        localStorage.setItem("leftMoney", this.reward);
      }
      console.log(localStorage.getItem("leftMoney"));
      console.log(222);
      this.isCalced = true;
    },
    // 随机生成题目
    generateQuestions() {
      // 0:"10以内", 1:"20以内", 2:"50以内", 3:"100以内"
      let level = this.questionLevel;
      let levelNum;
      switch (level) {
        case 0:
          levelNum = 10;
          break;
        case 1:
          levelNum = 20;
          break;
        case 2:
          levelNum = 50;
          break;
        case 3:
          levelNum = 100;
          break;
        default:
          levelNum = 10;
          break;
      }
      // 0："加法",1："减法", 2："加减混合"
      let type = this.questionType;
      let questionStr = "";

      this.readyFlag = true;
      this.startFlag = false;
      this.overFlag = false;
      this.isCalced = false;

      let operatorIndex, num1, num2, num3, ope1, ope2;
      for (var i = 0; i < this.questionsCount; i++) {
        // mix
        operatorIndex = Math.round(Math.random());
        num1 = Math.ceil(Math.random() * levelNum);
        num2 = Math.ceil(Math.random() * levelNum);
        num3 = Math.ceil(Math.random() * levelNum);
        ope1 = ["+", "-"][operatorIndex];
        ope2 = ["+", "-"][operatorIndex];
        switch (type) {
          case 0:
            // plus
            questionStr = num1 + " + " + num2;
            break;
          case 1:
            // minus
            if (num1 > num2) {
              questionStr = num1 + " - " + num2;
            } else {
              questionStr = num2 + " - " + num1;
            }
            break;
          case 2:
            // mix
            // if (ope1 == "-" && num1 < num2) {
            //   num1 = this.changeNum(num1, num2)[0];
            //   num2 = this.changeNum(num1, num2)[1];
            //   // num2 = Math.ceil(Math.random() * num1);
            // }
            // if (ope2 == "-" && eval(num1 + ope1 + num2) < num3) {
            //   num3 = Math.ceil(Math.random() * eval(num1 + ope1 + num2));
            // }
            // 题目质量改善（算法变更）
            ope1 = "+";
            ope2 = "-";
            if (num3 > num1 + num2) {
              num3 = Math.ceil(Math.random() * (num1 + num2));
            }
            questionStr =
              num1 + " " + ope1 + " " + num2 + " " + ope2 + " " + num3;
            break;
        }
        this.totalCount++;
        this.questionsArray.push({
          // question: num1 + " " + ope1 + " " + num2 + " " + ope2 + " " + num3,
          question: questionStr,
          answer: eval(questionStr),
          checkResult: 2,
        });
      }
    },
    // 交换value1和value2的值
    changeNum(value1, value2) {
      return [value2, value1];
    },
    // 重新生成题目
    resetQuestions() {
      this.readyFlag = false;
      this.startFlag = false;
      this.overFlag = false;
      this.totalCount = 0;
      this.correctCount = 0;
      this.scores = 0;
      this.questionsArray = [];
      this.stopTimer();
      document.getElementById("spendTime").innerHTML = "00:00:00";
    },
    // 开始答题
    start(model) {
      // 倒计时秒数
      this.limitTimeSeconds = this.limitTime * 60;
      this.startFlag = true;
      this.overFlag = false;
      this.readyFlag = false;

      // 计时开始
      if (this.timmer != "") {
        this.stopTimer();
      }
      // init
      this.hour = 0;
      this.minite = 0;
      this.seconds = 0;

      // 计时模式
      if (model == 1) {
        this.timmer = setInterval(this.startTimer, 1000);
      } else if (model == 2) {
        // 倒计时模式
        this.timmer = setInterval(this.countDown, 1000);
      }
    },
    startTimer() {
      this.seconds++;
      if (this.seconds == 60) {
        this.seconds = 0;
        this.minite++;
      }
      if (this.minite == 60) {
        this.minite = 0;
        this.hour++;
      }
      document.getElementById("spendTime").innerHTML =
        (this.hour < 10 ? "0" + this.hour : this.hour) +
        ":" +
        (this.minite < 10 ? "0" + this.minite : this.minite) +
        ":" +
        (this.seconds < 10 ? "0" + this.seconds : this.seconds);
    },
    stopTimer() {
      clearInterval(this.timmer);
    },
    //需要传入的参数为秒数，此方法倒计时结束后会自动刷新页面
    countDown() {
      var t = this.limitTimeSeconds;
      // 倒计时结束
      if (t == 0) {
        // this.stopTimer();
        this.submit();
      }
      console.log("t=" + t);
      this.hour = Math.floor((t / 60 / 60) % 24);
      this.hour < 24 && (this.hour = "0" + this.hour);
      this.minite = Math.floor((t / 60) % 60);
      this.minite < 10 && (this.minite = "0" + this.minite);
      this.seconds = Math.floor(t % 60);
      this.seconds < 10 && (this.seconds = "0" + this.seconds);
      document.getElementById("spendTime").innerHTML =
        this.hour + ":" + this.minite + ":" + this.seconds;
      console.log(this.hour + ":" + this.minite + ":" + this.seconds);
      this.limitTimeSeconds--;
    },
  },
  computed: {
    today() {
      let myDate = new Date();
      let year = myDate.getFullYear();
      let month = myDate.getMonth() + 1;
      let day = myDate.getDate();
      return year + "/" + month + "/" + day;
    },
  },
  filters: {
    standerAnswer(value) {
      return eval(value);
    },
    removeMinus: function (value) {
      if (value == 0) {
        return value;
      }
      return value.toString().substring(1);
    },
  },
  created() {
    // 判断是否已经登录，如果没有登录，则跳转到登录画面
    var loginFlag = sessionStorage.getItem("loginFlag");
    if (loginFlag == null) {
      this.$router.push({ name: "login" });
    }

    if (localStorage.getItem("leftMoney") != null) {
      this.leftMoney = localStorage.getItem("leftMoney");
    }
  },
};
</script>

<style src="@/assets/css/math.css"></style>




