<template>
  <div class="container login">
    <div class="title">
      <h3>我的学习乐园</h3>
    </div>
    <p class="login-error" v-show="hasError">
      {{ loginErrMsg }}
    </p>
    <p>
      <input
        type="text"
        class="user-name"
        placeholder="用户名"
        v-model="userName"
      />
    </p>
    <p>
      <input
        type="password"
        class="password"
        placeholder="密码"
        v-model="password"
      />
    </p>
    <input type="button" value="登录" class="btn btn-login" @click="login" />
    <button class="forget-pwd" @click="forgetPwd"><u>我忘记了密码</u></button>
    <p class="forget-pwd-content" v-show="forgetPwdFlag">
      如果忘记密码请联系你的爸爸或者妈妈进行解锁，每次解锁费用<b>{{
        resetPwdPrice
      }}</b
      >元！！！
    </p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userName: "",
      password: "",
      forgetPwdFlag: false,
      hasError: false,
      resetPwdPrice: 2,
      loginErrMsg: "用户名或密码错误，请重新输入",
    };
  },
  methods: {
    forgetPwd() {
      this.forgetPwdFlag = !this.forgetPwdFlag;
    },
    login() {
      this.hasError = false;
      if (this.userName != "张" || this.password != "7070") {
        this.hasError = true;
        return;
      }
      // 跳转到top画面
      this.$router.push({ name: "top", params: { userName: this.userName } });
      // 登录信息保存到缓存中
      sessionStorage.setItem("loginFlag", true);
    },
  },
  created: function () {
    console.log("created");
    this.$emit("header", false);
    var loginFlag = sessionStorage.getItem("loginFlag");
    if (loginFlag != null && loginFlag) {
      // 跳转到top画面
      this.$router.push({ name: "top", params: { userName: this.userName } });
    }
    console.log("created over");
  },
};
</script>
<style src="@/assets/css/login.css"></style>



