<template>
  <div class="container">
    <!-- <h3>欢迎{{ userName }}</h3> -->
    <marquee><h3 class="welcome">欢迎来到嘉程的小屋！！！</h3></marquee>
    <div class="subject">
      <p class="subject-title">我的科目</p>
      <hr />
      <button @click.stop="goto('math')">数学</button>
      <button @click.stop="goto('japanese')">日语</button>
      <button @click.stop="goto('chinese')">语文（建设中）</button>
      <button @click.stop="goto('english')">英语（建设中）</button>
    </div>
    <div class="show-image">
      <p class="image-title">我的照片墙</p>
      <hr />

      <div class="row">
        <div
          v-for="(item, index) in images"
          :key="index"
          class="col-xs-6 col-md-3"
        >
          <img class="thumbnail" :src="item" />
        </div>
      </div>
    </div>
    <div class="info">
      <p class="info-title">我的信息</p>
      <hr />
      建设中...
    </div>
    <div class="score">
      <p class="score-title">我的得分</p>
      <hr />
      建设中...
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      userName: this.$route.params.userName,
      images: [
        "static/img/001.JPG",
        "static/img/002.JPG",
        "static/img/003.JPG",
        "static/img/004.JPG",
        "static/img/005.JPG",
        "static/img/006.JPG",
        "static/img/007.JPG",
        "static/img/008.JPG",
        "static/img/009.JPG",
        "static/img/010.JPG",
        "static/img/011.JPG",
        "static/img/012.JPG",
        "static/img/013.JPG",
      ],
    };
  },
  methods: {
    goto(subject) {
      console.log(subject);
      this.$router.push({ name: subject });
    },
  },
  created() {
    // 判断是否已经登录，如果没有登录，则跳转到登录画面
    var loginFlag = sessionStorage.getItem("loginFlag");
    if (loginFlag == null) {
      this.$router.push({ name: "login" });
      // this.$router.push({ path: "/" });
    }
  },
};
</script>
<style src="@/assets/css/top.css"></style>



